@import "https://fonts.googleapis.com/css2?family=Hanken+Grotesk:wght@500;700;800&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

body {
  font-family: Hanken Grotesk, sans-serif;
}

.container {
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

header {
  width: 100%;
  text-align: center;
  background: linear-gradient(#7857ff, #2e2be9);
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  padding: 2.4rem 0 4rem;
  box-shadow: 0 3rem 6rem #3d6cec26;
}

