  color: #cac9ff;
  margin-bottom: 2.4rem;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 2.3rem;
}

header .result {
  height: 14rem;
  width: 14rem;
  background: linear-gradient(#4e21ca, #2421ca00);
  border-radius: 50%;
  margin: 0 auto;
  padding: 2.5rem 0;
}

header .result .total-score {
  color: #fff;
  font-size: 5.6rem;
  font-weight: 800;
  line-height: 7.2rem;
}

header .result .max-score {
  color: #cac9ff;
  opacity: .52;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 2.1rem;
}

header .message {
  width: 26rem;
  margin: 2.4rem auto 0;
}

header .message h2 {
  color: #fff;
  margin-bottom: .8rem;
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 3.1rem;
}

header .message p {
  color: #cac9ff;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 2.1rem;
}

main {
  width: 100%;
  margin-top: 2.4rem;
  padding: 0 3.2rem;
}

main h3 {
  color: #303b59;
  margin-bottom: 2.4rem;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 2.3rem;
}

main ul {
  list-style: none;
}

main ul li {
  border-radius: 1.2rem;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.6rem;
  padding: 1.6rem;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 2.1rem;
  display: flex;
}

main ul li:last-child {
  margin-bottom: 0;
}

main ul li.reaction {
  color: #f55;
  background: linear-gradient(#fffffff2, #fffffff2), #f55;
}

main ul li.memory {
  color: #ffb21e;
  background: linear-gradient(#fffffff2, #fffffff2), #ffb21e;
}

main ul li.verbal {
  color: #00bb8f;
  background: linear-gradient(#fffffff2, #fffffff2), #00bb8f;
}

main ul li.visual {
  color: #1125d6;
  background: linear-gradient(#fffffff2, #fffffff2), #1125d6;
}

main ul li .category {
  align-items: center;
  display: flex;
}

main ul li .category span {
  margin-left: 1.2rem;
}

main ul li .score {
  color: #303b59;
}

main ul li .score .max-score {
  opacity: .5;
  margin-left: .3rem;
}

main button {
  width: 100%;
  color: #fff;
  background: #303b59;
  border: none;
  border-radius: 12.8rem;
  margin: 2.4rem 0;
  padding: 1.6rem 0;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 2.3rem;
}

main button:hover {
  cursor: pointer;
  background: linear-gradient(#75f 0%, #6943ff .01%, #2f2ce9 100%);
}

